.Heading {
  font-weight: 500;
  font-size: 18px;
  line-height: 35px;
  text-align: center;
  color: #FFFFFF;
  white-space: nowrap;
  display: flex;
  &:before, &:after {
    display: block;
    content: "";
    margin-top: 18px;
    width: 100%;
    height: 1px;
    background-color: #FFFFFF;
    opacity: 0.25;
  }
  &:before {
    margin-right: 12px;
  }
  &:after {
    margin-left: 12px;
  }
}